.singleProject{
    width: 95%;
    margin: 0.5rem;
    font-family: Raleway;
  }
  .boldHeading{
      font-weight: 500;
      font-size: 1.2em;
  }

  .whiteFont{
    color:#EEEEEE !important;
    font-size: 2rem !important;
  }

  .tealFont{
      color: #1DE9B6!important;
      font-size: 1.2em !important;
  }

  .greyFont{
    color: #9E9E9E !important;
    font-size: 0.9rem !important;
  }

  .description{
    font-size: 0.9em !important;
  }

  .projectImage{
      width: 30%;
      border-radius: 10px;
  }

  .singlePortfolioImage{
      height: 50px;
      background-position: top;
      background-size: cover;
      border-radius: 10px;
      width: 50px;
      margin: 3px;
  }
  .singlePortfolioImage a{
    height: 100%;
    width: 100%;
}


  .portfolioImages{
    height: 100%;
    width: 100%;
      display: -ms-flexbox;
      display: flex;
      /* flex-direction: column; */
      -ms-flex-pack: distribute;
          justify-content: space-around;

  }


  .picturePorfolioDiv{
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .descPortfolioDiv{
      width: 100%;
  }

  .carousel{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .carousel .thumb.selected, .carousel .thumb:hover{
    border: 3px solid rgb(29, 233, 182)!important;
  }

  .thumb {
    border: 3px solid black !important;
  }
  

  @media only screen  /*Mobile Larger */
  and (min-width:375px )
  and (max-width:605px) {

    .row {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      width: 100%;
      -ms-flex-pack: justify;
          justify-content: space-between;
      -ms-flex-align: center;
          align-items: center;
      border-top: 2px solid grey;
      padding-top: 10px;
  }

  .rowReverse{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
