        width: 100%;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-align: center;
            align-items: center;
        border-top: 2px solid grey;
        padding-top: 10px;
  }

    .singlePortfolioImage{
      height: 70px;
      background-position: top;
      background-size: cover;
      border-radius: 20px;
      width: 70px;
      margin: 3px;
  }

  .description{
    font-size: 0.9em !important;
  }
}




